.wrapper {
  margin-top: 6rem;
  text-align: center;
}

.para {
  font-size: 2.5rem;
  color: black;
  margin-bottom: 4rem;
}

.btn {
  display: inline-block;
  font-size: 2rem;
  padding: 1rem 2rem;
  border-radius: 3rem;
  cursor: pointer;
  border: none;
  background-color: gray;
  color: black;
  text-transform: uppercase;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
