@import url("https://fonts.googleapis.com/css2?family=Oswald&display=swap");

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Oswald", sans-serif;
}
.lock {
  overflow: hidden;
  height: 100vh;
}

html {
  font-size: 62.5%;
}

ul {
  list-style: none;
}
